.board-container {
  font-size: 0px;
  width: 150px;
  height: 150px;
  margin: auto;
  margin-top: 75px;
  border: 2px solid black;
  
}

.cell {
  width: 50px;
  height: 50px;
  display: inline-block;
  font-size: 0;
  background: #00d0ff;
  border: 1px solid black;
  box-sizing: border-box;
}

.cell:hover {
  cursor: pointer;
  background: yellow;
}

.cell[data-player="1"]{
  background: green;
}

.cell[data-player="2"]{
  background: red;
}